import styled from 'styled-components'

export const MainDiv = styled.form`
width:90%;
background-color:#5965E1;
height:15vh;
margin: 1rem auto;
border-radius:1rem;
display:flex;
flex-direction:row;
align-items:center;
justify-content:space-evenly;
`

export const Inputtext = styled.input`
width:20%;
padding:0.5rem;
padding-left:1rem;
border-radius:0.3rem;
border:none;
:focus{
    outline:none;
}
`

export const Inputbutton = styled.input`
width:8%;
padding:1rem 0;
background-color:#415271;
border:none;
border-radius:1rem;
color:white;
transition:0.2s;
:focus{
  border:none;
  outline:none;
}
:hover{
background-color:#FCA311;
border:none;
color:black;
}
`
export const Durum =styled.div`
position:relative;
height:5vh;
width:10%;
`
export const DurumOption = styled.option`
border-radius:0.5rem;
-webkit-border-radius:0.5rem;
`
export const Iclass=styled.i`
top:50%;
left:85%;
height:100%;
position:absolute;
pointer-events:none;
`
export const TodoText=styled.textarea`
width:20%;
padding:0.5rem;
padding-left:1rem;
border-radius:0.3rem;
border:none;
:focus{
    outline:none;
}
resize: none;
height:10vh;
`
export const Dropdown = styled.select`
appearance:none;
width:100%;
padding:1rem;
outline:none;
-webkit-outline:none;

:active{
  -webkit-border:none;
  -moz-border:none;
  border:none;
  -webkit-outline:none;
  -moz-outline:none;
  outline:none;

}

`
