import styled ,{keyframes}from 'styled-components'

export const TodoMain = styled.div`
display:flex;
flex-direction:row;
align-items:center;
flex-wrap:wrap;
justify-content:space-evenly;
position:relative;
transition:1s ease;
`

export const Todos = styled.div`
width:25%;
background-color:#ED563B;
border-radius:1rem;
position:relative;
margin:1rem;
height:50vh;
`
export const Todoh1=styled.h1`
display:block;
text-align:center;
color:#FCA311;
`

export const Todotext=styled.p`
color:white;
display:block;
max-width:100%;
text-align:center;

text-overflow:inherit;
`
const haralet = keyframes`
0%{
    margin-bottom:0rem;
}
100%{
    margin-bottom:2rem;
}
`

export const Todul=styled.ul`
position:sticky;
width:100%;
left:0;
bottom: 0;
`
export const Todasagı = styled.p`
position:sticky;
right:0;
bottom:0;
margin:20px;
z-index:1;
`

export const Todonull= styled.li`
background-color:#f51d4c;
color:white;
display:flex;
flex-direction:row;
align-items:center;
position:sticky;
height:5vh;
width:20%;
margin:2rem;

animation: 0.5s ${haralet} ease;
p{
    margin: 0 auto;
    display:inline-block;
    align-items:center;
}
div{
    display:relative;
    p{
        position:absolute;
        cursor:pointer;
        top:0;
        right:0;
    }
}
`
