import React from "react";
import { useSelector } from "react-redux";
import {
  Deneme,
  Todasagı,
  Todoh1,
  TodoMain,
  Todonull,
  Todos,
  Todotext,
  Todul,
} from "./Todostyleto";
function todoRenk(secim){
  switch(secim){
    case "Bitmiş":
      return("green")
    case "Bitmemiş":
      return("yellow")
    case "Başlanmadı":
      return ("red")
    default:
      return ("v")
  }
}
function todoRenkText(secim){
  switch(secim){
    case "Bitmiş":
      return("Bitmiş")
    case "Bitmemiş":
      return("Bitmemiş")
    case "Başlanmadı":
      return ("Başlanmadı")
    default:
      return ("v")
  }
}
const Todo = () => {
  const state = useSelector(state => state)
  return (
    <div>
      <TodoMain>
        {
          state.todoReducer&&state.todoReducer.map((en,i)=>{
            if(en[0]!==""&&en[0]!==undefined){
              return(
                <Todos key={i} className={todoRenk(en[2])
                }>
                  {
                    
              console.log(todoRenk(en[2])+"===="+en[2])
            }
                <Todoh1>{en[0]}</Todoh1>
                <Todotext>{en[1]}</Todotext>
                <Todasagı>{
            todoRenkText(en[2])
            }</Todasagı>
              </Todos>
              )
            }else{
              return(null)
            }
          })
        }
      </TodoMain>
      <div className="sticky">
      <Todul>
      {
      state.todoReducer&&state.todoReducer.map((en,i)=>{
        console.log(en[0]);
        if(en[0]===undefined||en[0]===""){
         return(
           <Todonull>
           <p>Değer Girmeyi Unuttunuz</p>
            <div>
              <p onClick={(e)=>{
                e.target.parentElement.parentElement.remove()
              }}>X</p>
              </div>
           </Todonull>)
        }else{
          return(null);
        }})
        
        }
        </Todul>
      </div>
    </div>
  );
};

export default Todo;
