import React from 'react'
import {Header,Title,Navui,NavElement,LoginBtn} from "./Nav-Element"

const Nav = () => {
    return (
        <div>
            <Header>
            <Title>Todo</Title>
            <Navui>
                <NavElement>
                    <p>Home</p>
                </NavElement>
                <NavElement>
                    <p>Todos</p>
                </NavElement>
                <LoginBtn>
                    <p>Login</p>
                </LoginBtn>
            </Navui>
            </Header>
        </div>
    )
}

export default Nav
