import styled from 'styled-components'

export const Button = styled.button`
margin:0;
`

export const Header = styled.div`
width:100%;
background-color: #5965E1;
height:10vh;
display:flex;
flex-direction:row;
justify-content:space-between;
align-items:center;
color:white;
`

export const Title = styled.h1`
width:30%;
text-align:center;
`

export const Navui = styled.ul`
list-style-type:none;
display:flex;
width:40%;
justify-content:space-evenly;

`
export const LoginBtn=styled.li`
background-color:#fca311;
cursor:pointer;
border-radius:1rem;
color: #414c71;
padding: 0.5rem;
padding-left:2rem;
padding-right:2rem;
`
export const NavElement = styled.li`
background-color:#415271;
border-radius:1rem;
padding: 0.5rem 1rem;
cursor:pointer;
transition: 0.5s ease;
:hover{
    -webkit-transform: translateX(-2px) translateY(2px);
            transform: translateX(-2px) translateY(2px);-webkit-transform: translateX(-8px) translateY(8px);
            transform: translateX(-2px) translateY(2px);
}
`
