import React, { useEffect, useState } from "react";
import {
  MainDiv,
  Inputtext,
  Inputbutton,
  Durum,
  DurumOption,
  Dropdown,
  Iclass,
  TodoText,
} from "./Todostyle";
import { useDispatch} from "react-redux";
import * as todoAction from "../Redux/Action/todoAction"
const Todoadd = () => {
  const [todo, setTodo] = useState();
  const [todotext, setTodotext] = useState();
  const [todoselect, setTodoselect] = useState();
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(todoAction.todoAdd([todo,todotext,todoselect]))
    return () => {
    }
  }, [dispatch,todo,todotext,todoselect])
  return (
    <div>
      <MainDiv>
        <Inputtext placeholder="Todo İsmini Giriniz" id="todoName" ></Inputtext>
        <TodoText type="text" placeholder="Todonun amacını giriniz '/' ile başlayınız" id="todoText" maxLength="50"></TodoText>
        <Durum>
          <Dropdown id="drop">
            <DurumOption>
              Bitmemiş
            </DurumOption>
            <DurumOption>
              Bitmiş
            </DurumOption>
            <DurumOption>
              Başlanmadı
            </DurumOption>
          </Dropdown>
          <Iclass className="fas fa-angle-double-down"></Iclass>
        </Durum>
        <Inputbutton type="button" value="Ekle" onClick={(e)=>{
          setTodo(document.getElementById("todoName").value);
          setTodotext(document.getElementById("todoText").value);
          setTodoselect(document.getElementById("drop").value)
        }}></Inputbutton>
      </MainDiv>
    </div>
  );
};

export default Todoadd;
